@import url('https://fonts.googleapis.com/css2?family=Fredoka:wght@300&family=Roboto+Condensed:wght@300&display=swap');
*{
  background-color: #ccccff;
  font-family: 'fredoka';
}
.container{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: 30px;
}
.textCont{
  font-weight: bold;
  font-size: 1.3rem;
}
.counter{
  display: flex;
  flex-direction: column;
  align-items: center;
  min-height: 100vh;
  align-items: center;
  justify-content: center;
  width: 50%;
}
.text{
  font-weight: bold;
  font-size: 3rem;
}
.btn{
  width: 100%;
  padding: 1%;
  background-color: white;
  border-color: white;
  border-radius: 10px;
  cursor: pointer;
  font-size: 2rem;
  font-weight: bold;
}